import {authAPI, securityAPI} from "../api/api";
import {stopSubmit} from 'redux-form'


const SET_USER_DATA = 'SET_USER_DATA'
const GET_CAPTCHA_URL_SUCCESS='GET_CAPTCHA_URL_SUCCESS'


let initialState  = {
    userId: null as number | null,
    email: null as string | null,
    login: null as string | null,
    isAuth: false,
    captchaUrl: null as string| null
}
export type InitialStateType = typeof initialState

const autorReducer = (state = initialState, action: any) : InitialStateType=> {

    switch (action.type) {
        case  SET_USER_DATA:
        case  GET_CAPTCHA_URL_SUCCESS:
            return {

                ...state,
                ...action.payload,
                
            }




        default :
            return state
    }
}
type setUserDataPayloadAtionType={
userId: number | null,
    email: string | null,
    login: string | null,
    isAuth:boolean ,
    captchaUrl:string | null
}
type setUserDataAtionType = {


        type: typeof SET_USER_DATA,
        payload: setUserDataPayloadAtionType

};
export const setUserData = (userId: number| null, email:string | null, login:string | null, isAuth:boolean, captchaUrl:any):setUserDataAtionType  => {

    return {
        type: SET_USER_DATA,
        payload: {userId, email, login, isAuth, captchaUrl}
    }
};
type getCaptchaUrlSuccessActionType = {


        type: typeof GET_CAPTCHA_URL_SUCCESS,
        payload: {captchaUrl:string}

};
const getCaptchaUrlSuccess = (captchaUrl:string) :getCaptchaUrlSuccessActionType => {

    return {

        type: GET_CAPTCHA_URL_SUCCESS,
        payload: {captchaUrl}
    }
};


export const setAuth = () => {
    return async (dispatch:Function) => {
        let response = await authAPI.me();
        if (response.data.resultCode === 0) {
            let {id, email, login} = response.data.data
            console.log(response.data)
            dispatch(setUserData(id, email, login, true, ''))

        }
    }

}
export const login = (email:any, password:any, rememberMe:any, captcha:any) => {

    return async (dispatch:Function) => {
        let response = await authAPI.login(email, password, rememberMe, captcha)
        console.log(response.data)
        if (response.data.resultCode === 0) {
            dispatch(setAuth())

        } else {
            console.log(response.data)
            if (response.data.resultCode === 10) {
                dispatch(getCaptchaUrl())
            }
            let message = response.data.messages.length > 0 ? response.data.messages[0] : 'Some error'
            dispatch(stopSubmit('login', {_error: message}))
        }
    }

}
export const getCaptchaUrl = () => {

    return async (dispatch:Function) => {
        let response = await securityAPI.getCaptchaUrl()
        const captchaUrl = response.data.url;
        dispatch(getCaptchaUrlSuccess(captchaUrl))

    }

}
export const logout = () => {
    return async (dispatch:Function) => {
        let response = await authAPI.logout();
        if (response.data.resultCode === 0) {
            let {id, email, login} = response.data.data
console.log(response.data)
            dispatch(setUserData( null, null, null, false, null))
        }
    }

}
export default autorReducer