import {UserAPI} from "../api/api";
import {updateObjectInArr} from "../utils/object-helper";
import {PhotosType} from "./profile-reducer";

const FOLLOWED = 'FOLLOWED'
const UNFOLLOWED = "UNFOLLOWED"
const SET_USERS = 'SET_USERS';
const SET_CURRENT_PAGE = 'SET_CURRENT_PAGE'
const SET_TOTAL_USER_COUNT = 'SET_TOTAL_USER_COUNT'
const LOADING = 'LOADING'
const TOGGLE_IS_FOLLOWING_PROGRESS = 'TOGGLE_IS_FOLLOWING_PROGRESS'

export  type UserType = {

    name: string,
    id: number,
    uniqueUrlName: null | string,
    status: string,
    followed: boolean
    photos: PhotosType
}
let initialState = {
    users: [] as Array<UserType>,
    pageSize: 20,
    totalUsersCount: 0,
    currentPage: 1,
    isFetching: false,
    followingInProgress: [] as Array<number>
}
type  initialStateType = typeof initialState


const usersReducer = (state = initialState, action: any):initialStateType => {

    switch (action.type) {

        case FOLLOWED:

            return {
                ...state,

                users: updateObjectInArr(state.users, action.id, "id", {followed: true})
            }
        case UNFOLLOWED:
            return {
                ...state,
                users: updateObjectInArr(state.users, action.id, "id", {followed: false})
                // users: state.users.map((el) => {
                //     if (el.id === action.id) {
                //         return {...el, followed: false}
                //     }
                //     return el
                // })
            }
        case SET_USERS:

            if (action.flag) {
                return {

                    ...state, users: [...state.users, ...action.users]
                }
            } else {
                return {

                    ...state, users: [...action.users]
                }
            }

        case SET_CURRENT_PAGE:

            return {
                ...state,
                currentPage: action.currentPage,
            }
        case SET_TOTAL_USER_COUNT:

            return {
                ...state,
                totalUsersCount: action.totalUsersCount,
            }
        case LOADING:
            return {
                ...state,
                isFetching: action.isFetching

            }
        case TOGGLE_IS_FOLLOWING_PROGRESS:
            return {
                ...state,
                followingInProgress: action.isProgress
                    ? [...state.followingInProgress, action.userId]
                    : state.followingInProgress.filter(id => id != action.userId)
            }
        default:
            return state
    }
}

export default usersReducer
type followType = {
    type: typeof FOLLOWED, id: number
};
const follow = (id: number):followType => {
    return {type: FOLLOWED, id: id}
};
type unfollowType = {
   type: typeof UNFOLLOWED, id: number
};
const unfollow = (id:number):unfollowType => {
    return {type: UNFOLLOWED, id: id}
};
type setUsersType =  {
   type: typeof SET_USERS, users: Array<UserType>, flag: boolean
};
export const setUsers = (users:Array<UserType>, flag:boolean):setUsersType => {
    return {type: SET_USERS, users: users, flag: flag}
};
type setCurrentPageType = {


        type: typeof SET_CURRENT_PAGE,
        currentPage: number

};
export const setCurrentPage = (page:number):setCurrentPageType => {

    return {
        type: SET_CURRENT_PAGE,
        currentPage: page
    }
};
type setTotalUsersCountType =  {


        type: typeof SET_TOTAL_USER_COUNT,
        totalUsersCount:number,


};
export const setTotalUsersCount = (count:number):setTotalUsersCountType => {

    return {
        type: SET_TOTAL_USER_COUNT,
        totalUsersCount: count,

    }
};
type setIsFetchingType = {


        type: typeof LOADING,
        isFetching: boolean

};
export const setIsFetching = (load:boolean):setIsFetchingType => {

    return {
        type: LOADING,
        isFetching: load
    }
};
type isToggleProgressType =  {
type: typeof TOGGLE_IS_FOLLOWING_PROGRESS, isProgress: boolean, userId: number
}
export const isToggleProgress = (flag:boolean, userId:number):isToggleProgressType => {
    return {type: TOGGLE_IS_FOLLOWING_PROGRESS, isProgress: flag, userId: userId}
}

export const getUsers = (currentPage:number, pageSize:number) => {
    return async (dispatch:any) => {
        dispatch(setCurrentPage(currentPage))
        dispatch(setIsFetching(true))
        // dispatch(isToggleProgress(true, userId))
        let data = await UserAPI.getUsers(currentPage, pageSize)
        dispatch(setIsFetching(false))
        dispatch(setUsers(data.items, false))
        dispatch(setTotalUsersCount(data.totalCount))
    }

}
export const followUnfollowFlow = async (dispatch:any, userId:number, apiMethod:any, actionCreator:any) => {
    dispatch(isToggleProgress(true, userId))

    let response = await apiMethod(userId)

    if (response.data.resultCode == 0) {
        dispatch(actionCreator(userId))
    }
    dispatch(isToggleProgress(false, userId))
}

