import {ProfileAPI, UserAPI} from "../api/api";
import {stopSubmit} from 'redux-form';

let updateNewTextPost = 'updateNewTextPost';
let addpost = 'ADD-POST';
let SET_USER_PROFILE = 'SET_USER_PROFILE'
let SET_STATUS = 'SET_STATUS'
let SET_PHOTO = 'SET_PHOTO'

type  postInfoType ={
    id : number,
    massage: string,
    like: number
}
type contactType={
    facebook: string
    website:string
    vk:string
    twitter:string
    instagram:string
    youtube:string
    github:string
    mainLink:string
}
 export  type PhotosType={
    small:string | null,
    large:string | null
}
type  profileType ={

    aboutMe:string,
    lookingForAJob: boolean,
    lookingForAJobDescription:string,
    fullName:string,
    userId:number,
    contacts: contactType,
    photos: PhotosType

}
let initialState = {
    postInfo: [
        {
            id: 1,
            massage: 'post1',
            like: 12
        },
        {
            id: 2,
            massage: 'post2',
            like: 20
        },
        {
            id: 3,
            massage: 'post3',
            like: 1
        },
    ] as Array<postInfoType>,
    profile: null as null | profileType,
    status: '',
    newPostText: ''

}
type  InitialStateType = typeof initialState

const profileReducer = (state = initialState, action:any):InitialStateType  => {

    switch (action.type) {
        case addpost:

            let newPost = {
                id: action.id,
                massage: action.text,
                like: 0
            };


            return {
                ...state,
                postInfo: [...state.postInfo, newPost],

            }
        case updateNewTextPost:

            return {
                ...state,
                newPostText: action.nextText
            }
        case SET_USER_PROFILE:

            return {
                ...state,
                profile: action.profile
            }
        case SET_STATUS:
            return {
                ...state,
                status: action.status
            }
        case SET_PHOTO:
            return {
                ...state,
                profile: {...state.profile, photos: action.photo} as profileType

            }
        default :
            return state
    }
}
type addPostTextType =  {
        type: typeof addpost,
        id: number,
        text:string

};
export const addPostText = (id:number, text:string):addPostTextType => {

    return {
        type: addpost,
        id: id,
        text
    }
};
type setStatusProfile =  {

        type: typeof SET_STATUS,
        status: string

};
export const setStatusProfile = (status:string):setStatusProfile => {

    return {
        type: SET_STATUS,
        status: status
    }
};
type updateNewPostTextType =  {

        type: typeof updateNewTextPost,
        nextText: string

};
export const updateNewPostText = (text:string):updateNewPostTextType => {

    return {
        type: updateNewTextPost,
        nextText: text
    }
};
type setUsersProfile ={type: typeof SET_USER_PROFILE, profile:profileType};
export const setUsersProfile = (profile: profileType ):setUsersProfile => ({type: SET_USER_PROFILE, profile});
type setUsersPhotoType = {type: typeof SET_PHOTO, photo: PhotosType};
export const setUsersPhoto = (photo: PhotosType):setUsersPhotoType => ({type: SET_PHOTO, photo});
export const setPhotoApi = (file: PhotosType) => {

    return async (dispatch: Function) => {


        let response = await ProfileAPI.photo(file)

        if (response.data.resultCode == 0) {
            console.log(response.data)
            dispatch(setUsersPhoto(response.data.data.photos))


        }
    }
}
export const getUsersProfile = (userId:number) => {

    return async (dispatch:Function) => {
        let response = await UserAPI.getProfile(userId)

        dispatch(setUsersProfile(response.data))

    }
}
export const getStatus = (userId:number) => {
    return async (dispatch:Function) => {

        let response = await ProfileAPI.getStatus(userId)
        dispatch(setStatusProfile(response.data))

    }
}
export const saveProfile = (profile: profileType) => {
    return async (dispatch:Function, getState:any) => {
        let id = getState().auth.userId
        let response = await ProfileAPI.saveProfile(profile)

        if (response.data.resultCode === 0) {
            dispatch(getUsersProfile(id))
        }else {
            console.log(response.data)
            let message = response.data.messages.length > 0 ? response.data.messages[0] : 'Some error'
            let nameContact = message.split('->').reverse()[0].slice(0, -1).toLocaleLowerCase()

             dispatch(stopSubmit('dataForm', {"contacts":{[nameContact] :  message} }))
            return Promise.reject(response.data.messages[0])
        }


    }
}
export const updateStatus = (status:string) => {

    return async (dispatch:Function) => {
        let response = await ProfileAPI.setStatus(status)
        if (response.data.resultCode === 0) {
            dispatch(setStatusProfile(status))
        }
    }
}
export default profileReducer