import React, {useState} from "react";

import {NavLink} from "react-router-dom";
import s from "./Users.module.css"




let User = ({ followingInProgress, unfollowing, following , user}) => {

    return <div className={s.usersWrapper}>


                    <div>
                        <div className={s.left}>
                            <span>{user.id}</span>
                            <div>
                                <NavLink to={'/profile/' + user.id}>
                                    <img
                                        src={user.photos.small !== null ? user.photos.small : 'https://www.kodefork.com/static/users/images/user.png'}
                                        alt=""/>
                                </NavLink>
                            </div>
                            <div>
                                {user.followed
                                    ? <button disabled={followingInProgress.some(id => id == user.id)}
                                              onClick={() => {
                                                  unfollowing(user.id)
                                              }}>unfollow</button>
                                    : <button disabled={followingInProgress.some(id => id == user.id)}
                                              onClick={() => {
                                                  following(user.id)
                                              }}>follow</button>}

                            </div>
                        </div>
                        <div className={s.right}>
                            <div>
                                <div>{user.name}</div>
                                <div>{user.status}</div>
                            </div>
                            <div>
                                <div>{"el.locations.city"}</div>
                                <div>{"el.locations.country"}</div>
                            </div>


                        </div>
                    </div>


        </div>



}

export default User