import React, {useState} from "react";
import s from "../../Users/Users.module.css";
import cn from 'classnames'
type PropsType={

    totalUsersCount: number
    pageSize: number
    setCurrentPage: (page:number)=> void,
    currentPage: number,

}
const Pagination : React.FC<PropsType> =({totalUsersCount, pageSize, setCurrentPage, currentPage, })=>{

    let pagesCount = Math.ceil(totalUsersCount / pageSize);
    let pages = []
    for (let i = 1; i <= pagesCount; i++) {
        pages.push(i)
    }
    console.log(pages)
    let portionSize = Math.ceil(pagesCount / 10)

    let [portionNumber, setPortionNumber] = useState(1)
    let leftNum = (portionNumber - 1) * 10 + 1
    let rightNum = portionNumber * 10
    let changePageNext = () => {
        let page = leftNum + 10
        setPortionNumber(portionNumber + 1)
        setCurrentPage(page)
    }
    let changePagePrev = () => {
        let page = leftNum - 10
        setPortionNumber(portionNumber - 1)
        setCurrentPage(page)
    }

    let currentClass
    return  <div className={s.pageNumbers}>

        {leftNum > portionNumber ? <button onClick={changePagePrev}>Prev</button> : ''}

        {pages.filter(p => p >= leftNum && p <= rightNum).map(p => {
            return <span onClick={() => {
                setCurrentPage(p)
                currentClass = s.activePage
            }} className={cn({[s.activePage]: currentPage === p }) }>{p}</span>
        })}

        {rightNum < pages.length ? <button onClick={changePageNext}>Next</button> : ''}

    </div>
}
export default Pagination